*{
    box-sizing: border-box;
}
body{
    margin: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: cursive;
    font-weight: 800;
}
.counter{
    text-align: center;
    font-size: 24px;
    margin-bottom: 24px;
}
button{
    margin-right: 24px;
    background-color: white;
    border-radius: 8px;
    font-family: inherit;
    font-weight: inherit;
    width: 72px;
    height: 32px;
    scale: 1;
    cursor: pointer;
}
button:hover{
    scale: 1.05;
}